import React from "react"
import bed from "../images/bed.svg"
import bath from "../images/bath.svg"
import area from "../images/area.svg"
import marker from "../images/location_marker.svg"
import { Link } from "gatsby"

const HomeProperty = ({ heading, content, properties, cta }) => {
  return (
    <div className="home_property_block">
      <div className="container">
        <h2 className="animate__animated animate__slideInUp">{heading}</h2>
        <p className="animate__animated animate__slideInUp">{content}</p>
        <div className="home_property_area">
          {properties.length > 0 &&
            properties.map((item, index) => {
              return (
                <a
                  href={item.slug}
                  className="home_property_box animate__animated animate__slideInUp"
                  key={index}
                >
                  <div className="home_property_thumb">
                    <img
                      src={item.featuredImage.node.sourceUrl}
                      alt={item.featuredImage.node.altText || item.title}
                      width={750}
                      height={442}
                    />
                  </div>
                  <div className="home_property_details">
                    <h3>{item.title}</h3>
                    <p>
                      <img
                        src={marker}
                        alt={item.propertySection.location}
                        width={25}
                        height={25}
                      />
                      <span>{item.propertySection.location}</span>
                    </p>
                    <div className="home_property_sec">
                      <div className="home_property_amenity">
                        <img
                          src={bed}
                          alt={properties.title}
                          width={25}
                          height={25}
                        />
                        <span>{item.propertySection.noOfRooms}</span>
                      </div>
                      <div className="home_property_amenity">
                        <img
                          src={bath}
                          alt={properties.title}
                          width={25}
                          height={25}
                        />
                        <span>{item.propertySection.noOfBathrooms}</span>
                      </div>
                      <div className="home_property_amenity">
                        <img
                          src={area}
                          alt={properties.title}
                          width={25}
                          height={25}
                        />
                        <span>{item.propertySection.totalArea}</span>
                      </div>
                    </div>
                    <div className="home_property_price">
                      {item.propertySection.price}
                    </div>
                  </div>
                </a>
              )
            })}
        </div>
        <Link
          to={cta}
          className="btn_default animate__animated animate__zoomIn"
        >
          Speak to an Advisor
        </Link>
      </div>
    </div>
  )
}

export default HomeProperty
