import React from "react"

const HomeJourney = ({ journeyHead, journeySteps, ctaLink, ctaLabel }) => {
  return (
    <div className="home_journey_block">
      <div className="container">
        <h2 className="animate__animated animate__slideInUp">{journeyHead}</h2>
        <div className="home_journey_area">
          {journeySteps.length > 0 &&
            journeySteps.map((item, index) => {
              return (
                <div className="home_journey_box" key={index}>
                  <div className="home_journey_left animate__animated animate__slideInUp">
                    <img
                      src={item.image?.node?.sourceUrl}
                      alt={item.image?.node?.altText || "Propey"}
                      width={585}
                      height={370}
                    />
                  </div>
                  <div className="home_journey_right animate__animated animate__slideInUp">
                    <div dangerouslySetInnerHTML={{ __html: item.content }} />
                  </div>
                </div>
              )
            })}
        </div>
        <a
          href={ctaLink}
          className="btn_default animate__animated animate__zoomIn"
        >
          {ctaLabel}
        </a>
      </div>
    </div>
  )
}

export default HomeJourney
