import React from "react"

const HomeSlider = ({ bannerImage, altText, heading, hmBannerContent }) => {
  return (
    <div className="home_slider_block">
      <div className="home_slider_sec">
        <img
          src={bannerImage}
          width={1152}
          height={690}
          loading="eager"
          alt={altText}
        />
      </div>
      <div className="container">
        <div className="home_slider_area">
          <h1 className="animate__animated animate__fadeInUp">{heading}</h1>
          <p className="animate__animated animate__fadeInUp animate__delay-05s">
            {hmBannerContent}
          </p>
        </div>
      </div>
    </div>
  )
}

export default HomeSlider
