import React from "react"

const HomeAbout = ({ count, caption, aboutHeading, content }) => {
  return (
    <div className="home_about_block">
      <div className="container">
        <div className="row home_about_area">
          <div className="col-4">
            <div className="home_about_count animate__animated animate__slideInUp">
              {count}
            </div>
            <div className="home_about_caption animate__animated animate__slideInUp">
              {caption}
            </div>
          </div>
          <div className="col-8">
            <h2 className="animate__animated animate__slideInUp">
              {aboutHeading}
            </h2>
            <p className="animate__animated animate__slideInUp">{content}</p>
          </div>
        </div>
      </div>
    </div>
  )
}

export default HomeAbout
